import {type} from "@testing-library/user-event/dist/type";

export const validationEmail = (values) => {
    const errors = {};

    if (!values.email) errors.email = ' email';

    else if (values.email
        && !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)
    ) errors.email = ' email';

    return errors;
}
export const validationText = (values) => {
    const errors = {};
    if (!values.name) errors.name = ' ';

    if (!values.text) errors.text = ' ';

    return errors;
}

export const validationDelivery = (values) => {
    const errors = {};
    //email
    if (!values.email) errors.email = '   ';
    else if (values.email
        && !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,63}$/i.test(values.email)
    ) errors.email = ' email';

    //phone
    if (!values.phone || !values.phone.replace(/[\s+375()_]/g, '').length) errors.phone = '   ';
    else if (values.phone) {
        let phone = values.phone.replace(/[\s()]/g, '')
        if (!/^(\+375)(29|25|44|33)(\d{3})(\d{2})(\d{2})$/.test(phone))
            errors.phone = '  !'
    }

    //country
    if (!values.country) errors.country = '   ';


    if (values.deliveryMethod !== 'store pickup') {
        //city
        if (!values.city) errors.city = '   ';
        //street
        if (!values.street) errors.street = '   ';
        //house
        if (!values.house) errors.house = '   ';
        //postcode
        if (values.deliveryMethod === 'pickup from post offices') {
            if (!values.postcode) errors.postcode = '   ';
        }
    }
    if (values.deliveryMethod === 'store pickup') {
        if (!values.storeAddress) errors.storeAddress = '   ';
    }
    //agree
    if (values.agree === false) errors.agree = '      ';

    return errors;
}

export const validationPayment = (values) => {
    const errors = {};
    if (values.paymentMethod === 'visa' || values.paymentMethod === 'mastercard') {
        if (!values.card || !values.card.replace(/[\s_]/g, '')) errors.card = '   ';
        else if (values.card.replace(/[\s_]/g, '').length !== 16) {
            errors.card = '   ';
        }
        if (!values.cardDate || !values.cardDate.replace(/[_/]/g, '')) errors.cardDate = '   ';
        if (values.cardDate) {
            let dateArr = values.cardDate.split('/');
            let month = dateArr[0].replace(/[_/]/g, '');
            let year = (20 + dateArr[1].replace(/[_/]/g, ''));
            if (month.length !== 2 || month > 12) errors.cardDate = '  ';
            if (year.length !== 4) errors.cardDate = '  ';
            else {
                let startDate = new Date();
                let valueDate = new Date(+year, +month);
                if (startDate > valueDate) errors.cardDate = '  ';
            }

        }
        if (!values.cardCVV) errors.cardCVV = '   ';
        else if (values.cardCVV.length < 3) errors.cardCVV = '   3 ';
        else if (values.cardCVV.length > 4) errors.cardCVV = '   4 ';
        else if (!values.cardCVV.replace(/\D+/g, '')) errors.cardCVV = ' ';
    }
    if (values.paymentMethod === 'paypal') {
        if (!values.cashEmail) errors.cashEmail = '   ';
        else if (values.cashEmail
            && !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,63}$/i.test(values.cashEmail)
        ) errors.cashEmail = ' email';
    }
    return errors;
}